
body {
  font-family: 'Lucida Sans Unicode', 'Arial', serif;
  font-size: larger;
  /* background-color: #1F2F48; */
  background-color: rgb(24, 49, 72);
  color: white;
  height: 100%;
  min-height: 100%;
  text-align: center;
}

html {
  height: 100%;
}

h1 {
  font-size: 80px;
}

.btn {
  font-size: 20px;
  background-color: rgb(57, 104, 105);
  color: whitesmoke;
}

#root, .App {
  min-height: 100%;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 70px;
}

.home {
  top: 0;
  position: relative;
}

.home-header {
  height: 800px;
  top: 0px;
}

.intro-header {
  padding: 0px;
}

.about-header {
  height: 500px;
}

.App-title {
  font-size: 2.5em;
}

.App-intro {
  font-size: large;
}

.block {
  width: 130px;
  margin: 0 auto;
  display: block;
}

header .btn {
  text-transform: uppercase;
}

header a:hover, .btn a:hover::before {
  text-decoration: none;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  box-shadow: 0 0 15px teal;
}

header a:hover, .btn a:hover {
  color: white;
  box-shadow: 0 0 0 yellow;
  text-shadow: 0 0 5px white;
}

header .btn, .services {
  margin-top: 100px;
}

header {
  padding-bottom: 50px;
  background-size: cover;
  text-align: center;
}

.about, .contact {
  padding: 150px 0;
}

.projects {
  background-position: center;
  background-size: cover;
  background-position: 0 0;
  background-repeat: no-repeat;
  background-image: url("/images/projects_background.jpg");
  box-shadow: 0px 100px 900px 50px rgb(24, 49, 72) inset, 0px -200px 200px 0px rgb(24, 49, 72) inset, 0px -100px 900px 50px rgb(24, 49, 72);
  /* box-shadow: 0px 100px 200px 70px rgb(24, 49, 72) inset; */
  padding-top: 100px;
}

