nav ul, .contact ul {
  list-style: none;
}

nav.navbar {
  position: absolute;
  width: 100%;
  text-align: right;
  right: 0;
  width: auto;
  padding-bottom: 100px;
  padding-right: 50px;
  color: white;
}

nav li {
  width: 100%;
}

nav a {
  position: center;
  width: 100%;
  height: 30px;
  padding: 10px 0;
  line-height: 30px;
}

nav li {
  width: auto;
  float: right;
  padding-top: 30px;
  padding-bottom: 50px;
}

a {
  box-shadow: white;
  color: white;
}

nav a {
  padding: 7px;
  border-right: 10px 1px;
  float: right;
  width: 100px;
  font-size: 18px;
}

footer {
  margin-top: 50px;
  padding-bottom: 20px;
  text-align: center;
}

.resume {
  width: 80%;
  text-align: center;
  padding: 0;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
}

.resume * {
  position: relative;
  margin-top: 50px;
  width: 80%;
  margin: auto;
}

.contacts {
  text-align: center;
  padding: 100px 100px 0 100px;
}

.contacts a {
  text-align: center;
  padding-right: 40px;
}

section a:hover, .btn a:hover {
  color: yellow;
  box-shadow: 0 0 0 yellow;
  text-shadow: 0 0 5px black;
}

h2 {
  /* font-family: "SF Pro Text", "SF Pro Icons", "Helvetica Neue", "Helvetica", "Arial", sans-serif; */
  font-size: 40pt;
  padding-bottom: 25px;
}

h3 {
  font-size: 25pt;
  padding-bottom: 25px;
}

.titleName {
  text-align: left;
  box-shadow: 0px 100px 100px 120px rgb(24, 49, 72) inset, 0px -10px 100px 100px rgb(24, 49, 72);
  margin-left: 50px;
  color: teal;
}

.title {
  margin-top: 100px;
  text-align: middle;
}

p {
  margin-left: 50px;
  text-align: left;
  padding-bottom: 150px;
}

img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 40%;
}
.projectBox {
  background-size: cover;
  width: 100%;
  background:  no-repeat center;
  margin: 0;
  position: relative;
  display: inline-block;
  border-radius: 10%;
}

.projectName {
  color: white;
  border: white 3px solid ;
  text-align: center; 
  display: inline-block;
  padding: 5px 20px;
  background: rgba(255,255,255,0.4);
  font-size: 20pt;
  font-family: "Garamond", sans-serif;
  position: relative;
  top: 35%;
 }

.projectsBox {
  -webkit-transition: ease-in-out 0.8s;
  transition: ease-in-out 0.8s;
  position: relative;
  margin: 0;
  width: 80;
   
}

.projectBox {
  -webkit-transition: ease-in-out 0.3s;
  transition: ease-in-out 0.3s;
  background-size: contain;
  background-color: darkslategray;
}

.projectBox:hover {
  -webkit-transform: scale(1.15);
          transform: scale(1.15);
  opacity: 1 !important;
}

.projectsBox {
  pointer-events: none;
}

.projectsBox .projectBox {
  pointer-events: auto;
}

.projectsBox:hover .projectBox {
  opacity: 0.5;
}

.projectsBox:hover .projectBox:hover {
  opacity: 1;
}
 

.description{ 
  margin-top: 25px;
  margin-bottom: 50px ;


}
.bubble {
	/*background-color: #00b4ff;*/
	width: 200px;
	height: 200px;
	border-radius: 50%;
	z-index: 100;
    margin-left: -100px;
    margin-top: -100px;
	position: "absolute";
	box-shadow: 0 20px 30px rgba(0, 0, 0, 0.2), inset 0px 10px 30px 5px rgba(255, 255, 255, 1);
    /*background: radial-gradient(ellipse at center, rgba(0,180,255, 0) 0%,rgba(255,255,255,0.5) 70%);*/
    pointer-events: none;
}

.bubble:after {
	background: radial-gradient(ellipse at center, rgba(0,180,255,0.45) 0%,rgba(255,255,255,0) 70%);
    /*background: radial-gradient(ellipse at center,  rgba(255,255,255,0.5) 0%,rgba(255,255,255,0) 70%);*/

	box-shadow: inset 0 20px 30px rgba(255, 255, 255, 0.3);
    border-radius: 50%;
    content: "";
    height: 180px;
    left: 10px;
    position: absolute;
    width: 180px;
}

